.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-content{
  background:rgb(80, 80, 189);
  padding:5% 10% 5% 10%;
  color:#fff;
  text-transform: uppercase;
}

.App-boxes{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:center;
      justify-content:center;
  padding:5% 0% 5% 0%;

}
.App-box{
  padding:5px;
  margin:5px;
  -webkit-box-shadow:4px 3px 2px #222;
          box-shadow:4px 3px 2px #222;
  width:360px;
  -webkit-animation: box-ease 2s;
          animation: box-ease 2s;
 
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@-webkit-keyframes box-ease{
  from{margin-top:50px;}
  to{margin-top: 0px;}
}
@keyframes box-ease{
  from{margin-top:50px;}
  to{margin-top: 0px;}
}


/*  ---------------------- Making responsive for mobile devices---------------- */
@media (max-width:600px){
  .App-boxes{
    -ms-flex-direction:column;
        flex-direction:column;

  }
  .App-box{
    width:auto;
  }
}


